import React, {useState, useEffect} from 'react';
import ContainerHeader from 'components/ContainerHeader/index';
import IntlMessages from 'util/IntlMessages';
import CardBox from 'components/CardBox/index';
import TextField from '@material-ui/core/TextField';
import Checkbox from '@material-ui/core/Checkbox';
import Button from '@material-ui/core/Button';
import {base_url, public_url, $http} from "config";
import {useSelector} from 'react-redux';
import CircularProgress from '@material-ui/core/CircularProgress';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import Fab from '@material-ui/core/Fab';
import SweetAlert from 'react-bootstrap-sweetalert'
import AttachFile from '@material-ui/icons/AttachFile'
import Resizer from 'react-image-file-resizer';

const CompanySettings = (props) => {

    const [loader, setLoader] = useState(false);
    const [company_name, setCompanyName] = useState("");
    const [website, setWebsite] = useState("");
    const [company_email, setCompanyEmail] = useState("");
    const [logo_img, setLogoImg] = useState(null);
    const [imagePreviewUrl, setLogoImgUrl] = useState('https://via.placeholder.com/300x300');
    const [BG_img, setBGImg] = useState(null);
    const [address1, setAddress1] = useState("");
    const [address2, setAddress2] = useState("");
    const [city, setCity] = useState("");
    const [status, setStatus] = useState("");
    const [is_upload, setIsUpload] = useState(false);
    const [front_cover, setFrontCover] = useState(null);
    const [back_cover, setBackCover] = useState(null);
    const [front_cover_name, setFrontCoverName] = useState("");
    const [back_cover_name, setBackCoverName] = useState("");
    const [postcode, setPostcode] = useState("");
    const [phone_number, setPhoneNumber] = useState("");
    const {width} = useSelector(({settings}) => settings);
    const [bg_img_url, setBGImgUrl] = useState('https://via.placeholder.com/300x300');
    const [goback, setGoback] = useState(false)

    useEffect(() => {
        $http.get(base_url + "company/getCompanyInfo")
        .then((response) => {
          setCompanyName(response.data.company.name);
          setWebsite(response.data.company.website);
          setCompanyEmail(response.data.company.company_email);
          setAddress1(response.data.company.address);
          setAddress2(response.data.company.address2);
          setCity(response.data.company.city);
          setStatus(response.data.company.status);
          setPostcode(response.data.company.postcode);
          setPhoneNumber(response.data.company.telephone);
          setFrontCoverName(response.data.company.front_cover);
          setBackCoverName(response.data.company.back_cover);
          setIsUpload(response.data.company.is_upload=='1'?true:false)
          setLoader(true);
          setLogoImgUrl(response.data.company.logo_img ? 
                (public_url + 'upload/img/' + response.data.company.logo_img) : 
                'https://via.placeholder.com/300x300');
          setBGImgUrl(response.data.company.bg_image ?
                (public_url + 'upload/img/' + response.data.company.bg_image) : 
                'https://via.placeholder.com/300x300');
        })
        .catch((error) => {
            setGoback(true)
        })
    
      }, []);
    const SaveCompany = () => {
        const frmData = new FormData();
        var user = JSON.parse(localStorage.getItem("user"));
        user.is_upload = is_upload?1:0
        if(front_cover)
            user.front_cover = company_name+'_front.'+front_cover_name.split('.').pop()
        if(back_cover)
            user.front_cover = company_name+'_back.'+back_cover_name.split('.').pop()
        //user.front_cover = front_cover?`${company_name}'_back'`
        localStorage.setItem("user",JSON.stringify(user))
        frmData.append('name', company_name);
        frmData.append('website', website);
        frmData.append('company_email', company_email);
        frmData.append('address', address1);
        frmData.append('address2', address2);
        frmData.append('city', city);
        frmData.append('status', status);
        frmData.append('postcode', postcode);
        frmData.append('telephone', phone_number);
        frmData.append('logo_img', logo_img);
        frmData.append('bg_image', BG_img);
        frmData.append('is_upload', is_upload?1:0);
        frmData.append('front_cover', front_cover);
        frmData.append('back_cover', back_cover);
        $http.post(base_url + "company/saveCompany", frmData)
        .then((response) => {
            if (response.data.status==="success") {
                NotificationManager.success("Save Company", "Success!", 1000, () => {
                });
            } else if (response.data.status === "error") {
                NotificationManager.error(response.data.msg, "Error!", 1000, () => {
                });
            }
        }).catch((error)=>{
            NotificationManager.error(error, "Error!", 1000, () => {
            });
        })

    }

    const resizeFile = (file) => new Promise(resolve => {
        Resizer.imageFileResizer(file, 500, 500, 'JPEG', 100, 0,
        uri => {
          resolve(uri);
        },
        'blob'
        );
      });
    
    const _handleImageChange = async (e) =>{
        e.preventDefault();

        let file = e.target.files[0];
        const image = await resizeFile(file);
        let img_file_name = file.name
        file = new File([image],img_file_name,image)
        setLogoImg(file);
        setLogoImgUrl(URL.createObjectURL(file));
  
        // let reader = new FileReader();
        // let file = e.target.files[0];
        // reader.onloadend = () => {
        //     setLogoImg(file);
        //     setLogoImgUrl(reader.result);
        // }
        // reader.readAsDataURL(file)
    }
    const _handleBGImageChange = async (e) =>{
        e.preventDefault();

        let file = e.target.files[0];
        const image = await resizeFile(file);
        let img_file_name = file.name
        file = new File([image],img_file_name,image)
        setBGImg(file);
        setBGImgUrl(URL.createObjectURL(file));
  
        // let reader = new FileReader();
        // let file = e.target.files[0];
        // reader.onloadend = () => {
        //     setBGImg(file);
        //     setBGImgUrl(reader.result);
        // }
        // reader.readAsDataURL(file)
    }

    return (
        <div className="app-wrapper">
            <ContainerHeader match={props.match} title={<IntlMessages id="sidebar.customers.company"/>}/>
            { loader ? (
                <div className="row">
                    <div className="col-xl-3 col-lg-4 col-md-5 col-12">
                        <div className="jr-card pb-2">
                            <div className="jr-card-thumb">
                                <img className="card-img-top img-fluid" alt="products" src={imagePreviewUrl}/>
                            </div>
                            <input type="file" id="logo_img" accept="image/*" 
                                name="logo_img" style={{display: "none"}}
                                onChange={(event) => {
                                    _handleImageChange(event);
                                }}/>

                            <div className="jr-card-social text-right">
                                <Button className="jr-btn text-primary">
                                    Company Logo
                                </Button>
                                <Fab className="jr-fab-btn bg-light-blue accent-2 text-white" onClick={(e)=>{document.getElementById('logo_img').click()}}>
                                    <i className="zmdi zmdi-cloud-upload zmdi-hc-2x"/>
                                </Fab>
                            </div>
                        </div>
                        <div className="jr-card pb-2">
                            <div className="jr-card-thumb">
                                <img className="card-img-top img-fluid" alt="products" src={bg_img_url}/>
                            </div>
                            <input type="file" id="BG_img" accept="image/*" 
                                name="BG_img" style={{display: "none"}}
                                onChange={(event) => {
                                    _handleBGImageChange(event);
                                }}/>

                            <div className="jr-card-social text-right">
                                <Button className="jr-btn text-primary">
                                    Company Background
                                </Button>
                                <Fab className="jr-fab-btn bg-light-blue accent-2 text-white" onClick={(e)=>{document.getElementById('BG_img').click()}}>
                                    <i className="zmdi zmdi-cloud-upload zmdi-hc-2x"/>
                                </Fab>
                            </div>
                        </div>
                    </div>

                <CardBox styleName="col-xl-9 col-lg-8 col-md-7 col-12">
                    <form className="row" autoComplete="off">
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="company_name"
                                label="Company Name"
                                value={company_name}
                                onChange={(event) => setCompanyName(event.target.value)}
                                margin="normal"
                                helperText="Please Enter Company Name"
                                fullWidth
                                />
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="website"
                                label="Website URL"
                                value={website}
                                onChange={(event) => setWebsite(event.target.value)}
                                margin="normal"
                                helperText="Please Enter Website URL"
                                fullWidth
                                />
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="company_email"
                                label="Company Email"
                                value={company_email}
                                onChange={(event) => setCompanyEmail(event.target.value)}
                                margin="normal"
                                helperText="Please Enter Company Email"
                                fullWidth
                                />
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="address1"
                                label="Address1"
                                value={address1}
                                onChange={(event) => setAddress1(event.target.value)}
                                margin="normal"
                                helperText="Please Enter Address1"
                                fullWidth
                                />
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="address2"
                                label="Address2"
                                value={address2}
                                onChange={(event) => setAddress2(event.target.value)}
                                margin="normal"
                                helperText="Please Enter Addres2"
                                fullWidth
                                />
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="city"
                                label="City"
                                value={city}
                                onChange={(event) => setCity(event.target.value)}
                                margin="normal"
                                fullWidth
                                />
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="state"
                                label="Status"
                                value={status}
                                onChange={(event) => setStatus(event.target.value)}
                                margin="normal"
                                fullWidth
                                />
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="postcode"
                                label="Postcode"
                                value={postcode}
                                onChange={(event) => setPostcode(event.target.value)}
                                margin="normal"
                                fullWidth
                                />
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="phone_number"
                                label="Phone Number"
                                value={phone_number}
                                onChange={(event) => setPhoneNumber(event.target.value)}
                                margin="normal"
                                fullWidth
                                />
                        </div>
                        <div className="d-flex align-items-center col-md-6 col-12">
                            <Checkbox 
                                id="is_upload"
                                color="primary"
                                checked={is_upload}
                                onChange={(event)=>setIsUpload(event.target.checked)}
                            /> 
                            <span>Upload Front/Back Pages</span>
                        </div>
                        <div className="col-md-6 col-12 d-flex align-items-center">
                            <input type="file" id="upload_front" 
                                accept="image/*" 
                                name="upload_front" style={{display: "none"}}
                                onChange={(event) => {
                                    setFrontCover(event.target.files[0]);
                                    setFrontCoverName(event.target.files[0].name);
                                }}/>
                            <Button className="attach-file jr-btn " color = "primary"
                                variant="contained" onClick={(e)=>{document.getElementById('upload_front').click()}}>
                                <AttachFile/> Upload Front
                            </Button>
                            <h2>{front_cover_name }</h2>
                        </div>
                        <div className="col-md-6 col-12 d-flex align-items-center">
                            <input type="file" id="upload_back" 
                                accept="image/*" 
                                name="upload_back" style={{display: "none"}}
                                onChange={(event) => {
                                    setBackCover(event.target.files[0]);
                                    setBackCoverName(event.target.files[0].name);
                                }}/>
                            <Button className="attach-file jr-btn " color = "primary"
                                variant="contained" onClick={(e)=>{document.getElementById('upload_back').click()}}>
                                <AttachFile/> Upload Back
                            </Button>
                            <h2>{back_cover_name }</h2>
                        </div>
                        <div className="col-md-12 col-12 text-right">
                            <Button variant="contained" color="primary" className="jr-btn" onClick={() => SaveCompany()}>
                                Save
                            </Button>
                        </div>
                    </form>
                </CardBox>
            </div>
            ) : (
                <div className="loader-view"
                        style={{height: width >= 1200 ? 'calc(100vh - 259px)' : 'calc(100vh - 238px)'}}>
                    <CircularProgress/>
                </div>        
            )}
            <SweetAlert show={goback}
                warning
                confirmBtnText="Go Back"
                confirmBtnBsStyle="danger"
                cancelBtnBsStyle="default"
                title="Warning!"
                onConfirm={() => History.go(-1)}
            >                
            Data not cached, so you can not view this page.
            </SweetAlert>

            <NotificationContainer/>
        </div>
    );
}

export default CompanySettings;